@import "src/App.scss";

.activeLine {
    width: 80%;
    height: 2px;
    position: absolute;
    bottom: -2px;
    background-color: $color-line;
    // transition: all 1s ease-out 0s;
}

.header {
    color: $font-color;
    display: flex;
    margin: 0 40px;
}

.nav {
    font-size: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;
    flex: 0 40%;
    cursor: pointer;
    div:active {
        transform: scale(1.1);
    }
}

.logo {
    font-size: 1.3rem;
    text-align: center;
}

.link {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: flex-end;
    gap: 2rem;
    flex: auto;
}

.img {
    position: relative;
    width: 1.3rem;
    height: 2rem;
    // cursor: pointer;
    img {
        object-fit: cover;
        max-width: 100%;
        max-height: 100%;
    }
}

.img::before {
    content: "";
    background: url("../../assets/link/elipse.svg") 0 0 / 100% no-repeat;
    position: absolute;
    width: 40px;
    height: 50px;
    top: -11px;
    right: -11px;
}

@include breakpoint(lg) {
    .nav {
        font-size: 1.2rem;
    }
    .link {
        gap: 1.5rem;
    }
    .logo {
        font-size: 1.1rem;
    }
    .img {
        width: 1.1rem;
        height: 1.1rem;
    }
    .img::before {
        width: 2.1rem;
        height: 2.5rem;
        top: -0.5rem;
        right: -0.5rem;
    }
}

@include breakpoint(md) {
    .activeLine {
        height: 1.8px;
    }
    .nav {
        font-size: 0.8rem;
        gap: 0.6rem;
    }

    .link {
        gap: 1.2rem;
    }

    .logo {
        font-size: 0.9rem;
    }

    .img {
        width: 1rem;
        height: 1rem;
    }

    .img::before {
        width: 1.8rem;
        height: 2.4rem;
        top: -0.3rem;
        right: -0.4rem;
    }
}

@include breakpoint(sm) {
    .activeLine {
        height: 1.5px;
    }
    .nav {
        font-size: 0.7rem;
        gap: 0.3rem;
    }

    .link {
        gap: 1.4rem;
    }

    .logo {
        font-size: 0.7rem;
    }
}
@include breakpoint(xs) {
    .header {
        margin: 0 20px;
    }
    .nav {
        font-size: 0.4rem;
        gap: 0.2rem;
        flex-direction: column;
        align-items: flex-start;
        flex: 0 30%;
    }

    .link {
        // gap: 1rem;
        height: 2rem;
    }

    .logo {
        font-size: 0.5rem;
    }

    .img {
        width: 0.5rem;
        height: 0.5rem;
    }

    .img::before {
        width: 1rem;
        height: 1.5rem;
        top: 0.3rem;
        right: -0.3rem;
    }
}